}

body {
    margin: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
}


/*button*/
.buttonPrimary {
    background-color: #ef6c00;
    width: 290px;
    height: 40px;
    border-radius: 3px;
    border: hidden;
    color: #ffffff;
    font-size: 14pt;
    font-weight: bold;
    font-family: 'Source Sans Pro', sans-serif;
    text-align: center;
    letter-spacing: 0.12ex;
    padding-top: 9px;
    cursor: pointer;
    -moz-user-select: none;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.buttonPrimary:active {
    color: #ffffff;
    border: hidden;
    background-color: #ff6d00;
}

.buttonSecondary {
    background-color: #ffffff;
    width: 290px;
    height: 40px;
    border-radius: 3px;
    border: solid 2px #ff6d00;
    color: #ff6d00;
    font-size: 14pt;
    font-weight: bold;
    font-family: 'Source Sans Pro', sans-serif;
    text-align: center;
    letter-spacing: 0.12ex;
    padding-top: 1px;
    cursor: pointer;
    -moz-user-select: none;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.buttonSecondary:active {
    background-color: #D3D3D3;
}

.buttonInactiv {
    background-color: #D3D3D3;
    width: 290px;
    height: 40px;
    border-radius: 3px;
    border: none;
    color: #696969;
    font-size: 14pt;
    font-weight: bold;
    font-family: 'Source Sans Pro', sans-serif;
    text-align: center;
    letter-spacing: 0.12ex;
    padding-top: 7px;
    cursor: pointer;
    -moz-user-select: none;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

button[disabled=disabled], button:disabled {
    background-color: #D3D3D3;
    width: 290px;
    height: 40px;
    border-radius: 3px;
    border: none;
    color: #696969;
    font-size: 14pt;
    font-weight: bold;
    font-family: 'Source Sans Pro', sans-serif;
    text-align: center;
    letter-spacing: 0.12ex;
    padding-top: 7px;
    cursor: pointer;
    -moz-user-select: none;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
}


.buttonText {
    background-color: #ffffff;
    color: #ef6c00;
    border: hidden;
    font-size: 16pt;
    font-weight: bold;
    font-family: 'Source Sans Pro', sans-serif;
    letter-spacing: 0.08ex;
    cursor: pointer;
    -moz-user-select: none;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.singOutButton {
    border: none;
    background-color: #ffffff;
}

